import uniListItem from "@/components/uni-list-item/uni-list-item.vue";
import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
export default {
	components: {
		uniListItem
	},
	data() {
		return {
			source: 0,
			addressList: [],
			scrollHeight: 100,
			tabScrollTop: 0,
			contentText: {
				contentdown: '上拉加载更多',
				contentrefresh: '加载中',
				contentnomore: '我是有底线的'
			},
			page:1,
			limit:20,
			status: 'more',
			cartId:0,
			id:0,
			num:0,
			adr:0,
		}
	},
	onShow(){
		this.addressList = [];
		this.userAddressIist();
	},
	onLoad(option){
		console.log(option.source);
		this.source = option.source;
		this.cartId = option.cartId
		
		this.id = option.id;
		this.num = option.num
		this.adr = option.adr
		
		
		getApp().user_api(true);
		// this.userAddressIist();
		this.calcSize();
	},
	methods: {
		
		userAddressIist(){
			let self = this;
			let path = '/gzh/user_api/user_address_list'
			let data = {page:self.page,limit:self.limit};
			getApp().http(path, data, function(result) {
				
				// self.addressList = result;
				console.log(result);
				
				
				let list = result;
				if (list.length > 0) {
					console.log(list.length)
					self.addressList = self.addressList.concat(list);
					self.status = list.length >= self.limit ? "more" : "";
					if (self.status == "more") {
						self.page += 1;
					}
					// self.status = list.length >= 20 ? "more" : "";
					// if (self.status == "more") {
					// 	self.page += 1;
					// }
					// self.addressList = self.addressList.concat(list);
				} else {
					self.status = "";
				}
				
				
				
			})
		},
		delAddress(e){
			let self = this;
			
			uni.showModal({
				title: '删除',
				content: '确定删除吗？',
				success: (md) => {
					if(md.confirm == true){
						console.log('确定')
						let path = '/gzh/user_api/remove_user_address';
						let data = {addressId:e};
						getApp().http(path, data, function(result) {
							
							console.log(result)
							console.log(result.code);
							console.log('------------00000000000000---------')
							
							if(result.code == 200){
								self.addressList = [];
								self.userAddressIist();
								getApp().showTip(result.msg,'success');
							}else{
								getApp().showTip(result.msg);
							}
						},true)
					}
				}
			})
			
			console.log(e);
			console.log('-----------------------------')
		},
		
		
		scroAddr(){
			let self = this;
			if(self.status){
				self.userAddressIist();
			}
		},
		//计算右侧栏每个tab的高度等信息
		calcSize(){
			var self = this;
			uni.getSystemInfo({
				success: function(res) { // res - 各种参数
					let info = uni.createSelectorQuery().select(".content");
					self.scrollHeight = res.windowHeight - 55
					// self.scrollHeight = 200
					console.log(info);
					console.log('啊啊啊啊',res);
				}
			});
		},
		
		
		//选择地址
		checkAddress(item){
			console.log('sdfsdfsdfsd',item);
			
			if(this.adr == 2){
				uni.navigateTo({
					url: `/pages/integral/integralOrder?id=`+this.id+'&num='+this.num+'&address='+item
				})
			}else if(this.adr == 3){
				uni.navigateTo({
					url: `/pages/order/createOrder?cartId=`+this.cartId+'&address='+item
				})
			}
		},
		addAddress(type, item){
			uni.navigateTo({
				url: `/pages/address/addressManage?type=${type}&data=${JSON.stringify(item)}`
			})
		},
		//添加或修改成功之后回调
		refreshList(data, type){
			//添加或修改后事件，这里直接在最前面添加了一条数据，实际应用中直接刷新地址列表即可
			this.addressList.unshift(data);
			
			console.log(data, type);
		}
	}
}
